import Loading from '../../public/Loading'
import  LoadingImg from '../../public/loadingImg'
import None from '../../public/noNumber'
  export default {
      name: "overInvite",
    data() {
        return{
          api: this.GLOBAL.baseURL,
          volunList:[],
          total:0,
          isLoading:false
        }
    },
    components:{
      None,
      LoadingImg,
      Loading
    },
    methods:{
      toPublish(item,index){
        this.$router.push({
          path: '/publishPage',
          query:{
            information: item.activity_id
          }
        })
      },
      handleCurrentChange(val){
        this.getOverlist(val)
      },
      getOverlist(val){
        this.isLoading = true
        this.$http.post(this.api + '/CompleteMy',{
          token: localStorage.getItem('token'),
          page: val
        })
          .then(res=>{
            if(res.data.code == 1){
              this.volunList = res.data.data.data
              this.total = res.data.data.total
              this.isLoading = false
            }else if(res.data.code == 3){
              this.$http.post(this.api + '/home/index/token')
                .then(res=>{
                  localStorage.setItem('token',res.data.data)
                  this.getOverlist(val)
                })
            }else if(res.data.code == 0){
              alert(res.data.msg)
              this.isLoading = false
            }
          })
      }
    },
    mounted(){
        this.getOverlist(1)
    }
  }



